      <p class="scrollable border p-3"  style={{fontFamily:'Playfair Display'}} >Fresh beetroot is a versatile and nutritious root vegetable, known for its vibrant purple color and earthy flavor. Often simply called beets, they are rich in essential vitamins and minerals, including folate, manganese, potassium, and vitamin C. The striking hue of beetroot not only adds a pop of color to dishes but also signals its high antioxidant content, particularly betalains, which may help reduce inflammation and oxidative stress in the body. Fresh beets can be enjoyed raw, cooked, or pickled, making them a staple in various cuisines worldwide. When raw, they can be grated into salads for a crunchy texture or juiced for a refreshing beverage. Cooking beets enhances their sweetness and softens their texture, making them perfect for roasting, boiling, or steaming. Roasted beetroot, drizzled with olive oil and sprinkled with herbs, is a popular dish that highlights their natural sugars. Additionally, they can be blended into soups, such as the classic borscht, a hearty beetroot soup popular in Eastern European cuisine.</p>
